.Feedback{
    height: 70vh;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
}

.form{
    display: flex;
    flex-direction: column;
}

.headingBlock{
    width: 50vw;
    margin: 0vw 15vw;
}

.headingText{
    font-size: 2em;
}

.writeBack{
    font-size: 1.5em;
    margin: 1em 0em;
}

.contacts{
    display: flex;
    flex-direction: row;
}

.textAreaWrapper{

}

.submitBtn{
    background-color: #333333 !important;
    text-transform: none !important;
    font-weight: normal !important;
    font-size: 1em;
    padding: 5px 10px;
    letter-spacing: normal !important;
    border-radius: 30px;
    box-shadow: none !important;
    transition: 0.5s all ease;
    cursor: pointer;
}

.submitBtn:hover{
    background-color: #bfbfbf !important;
    color: black;
}

.input{
    background-color: #e6e6e6;
    box-shadow: none;
    border: 1px #bfbfbf solid;
    margin-left: 10px;
    border-radius: 5px;
    font-size: 1em;
    padding: 10px 10px;
    outline: none;
}

.textArea{
    width: 100%;
    height: 20vh;
    resize: none;
    font-size: 1em;
}

.btnWrapper{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

label{
    font-size: 1.5em;
}

/*Laptop*/
@media all and (max-width: 1690px) {

}

/*iPad Pro*/
@media all and (max-width: 1280px) {
    .Feedback{
        height: 70vh;
    }

    .headingBlock{
        width: 80vw;
        margin: 0vw 5vw;
    }

    .headingText{
        font-size: 3em;
    }

    .writeBack{
        font-size: 3em;
        margin: 1em 0em;
    }

    .form{
        width: 100vw;
    }

    .contacts{
        flex-direction: column;
    }

    .submitBtn{
        font-size: 2em;
        margin-right: 1em;
        padding: 5px 10px;
    }

    .input{
        width: 82%;
        border-radius: 5px;
        font-size: 1em;
        padding: 10px 10px;
    }

    .textArea{
        width: 100%;
        height: 10vh;
        font-size: .75em;
    }

    label{
        font-size: 2em;
    }
}

/*iPad*/
@media all and (max-width: 980px) {

    .headingText{
        font-size: 2em;
    }

    .writeBack{
        font-size: 2em;
        margin: 1em 0em;
    }

    .submitBtn{
        font-size: 2em;
    }

    .input{
        width: 76%;
    }
}

/*Mobile*/
@media all and (max-width: 480px) {
    .headingText{
        font-size: 1em;
    }

    .submitBtn{
        font-size: .75em;
    }

    .input{
        width: 70%;
        font-size: 1em;
    }

    .textArea{
        width: 100%;
        height: 10vh;
        font-size: .75em;
    }

    label{
        font-size: 1em;
    }
}
